@import url('https://fonts.googleapis.com/css2?family=Hi+Melody&display=swap');
* {
  padding: 0;
  margin:0;
  box-sizing: border-box;
  text-align: unset;
}
body{
  background-color: rgb(233 236 239 / 45%);
  font-family: "Hi Melody", sans-serif;
  font-weight: 400;
  font-style: normal;
  height: 100vh;
  overflow: hidden;
}
#root , .App{
  height: 100%;
}
.App {
  margin: 0 auto;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #61dafb;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.mainListContainer {
  margin: 0 auto;
  max-width: 390px;
  height: calc(100% - 115px);
  overflow: auto;
}
.black-nav{
  background-color: #25324b;
  width: 100%;
  height: 60px;
  text-align: left;
  padding-left: 20px;
  display: inline-flex;
  align-items: center;
}
.black-nav> div{
  color: #fff;
  font-weight: bold;
  font-size: 35px
}
/*  */
.make-list {
  position: relative;
  padding: 20px;
  border-bottom: 1px solid #eee;
  margin: 20px;
  border-radius: 13px;
  border: 1.2px solid #e8e8e8;
  box-shadow: 0 1px 6px rgba(0,0,0,.08);
  background-color: #fff;
  min-height: 110px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.make-list > button{
  position: absolute;
  bottom: 15px;
  right: 20px;
  width: 40px;
  height: 40px;
  background: none;
  border:0;
  font-size: 16px;
  font-weight: bold;
}
.make-list > img{
  width: 140px;
  height: auto;
  text-align: center;
  margin: 0 auto 12px;
}
.make-list h4{
  font-size: 18px;
  text-align: center;
  margin-bottom: 8px;
}
.make-list > p {
  margin-bottom: 8px;
}
.make-list > p > span{
  padding:0 10px;
  border-left: 1px solid #999;
}
button.delate {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 26px;
  font-weight: bolder;
  color: #25324b;
}
/* 바텀시트 */
.open-here{
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 50px;
  border: 0;
  box-shadow: -1px -1px 7px 2px rgba(0,0,0,.08);
  background-color: #fff;
  text-align: center;
  color: #0057ff;
  font-weight: bold;
  cursor: pointer;
}
#bottom-sheet {
  background-color: #fff;
  height: 360px;
  border-radius: 32px 32px 0 0;
  border: 4px solid #0057ff;
  border-top-width: 5px;
  border-bottom: 0 ;
  position: fixed;
  bottom: -100%;
  left: 0;
  right: 0;
  box-shadow: -3px -5px 7px 2px rgba(0,0,0,.08);
  transition: all .5s ease-in-out;
}
#bottom-sheet.open{
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 2;
}
#bottom-sheet h4 {
  height: 60px;
  line-height: 60px;
  padding: 0 20px;
}
#bottom-sheet .content {
  padding:20px;
}
.dimm {
  display: block;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #000;
  opacity: 0.5;
  z-index: 1;
}
.code-box {
  background-color: #000;
  color: #eee;
  font-size: 21px;
  padding: 10px 20px;
  margin-top: 24px;
  border-radius: 10px;
}
#bottom-sheet > button.material-symbols-outlined {
  position: absolute;
  top: 18px;
  right: 20px;
  cursor: pointer;
  border:0;
  background:none;
}
/* modal */
.modal {
  z-index: 2;
  width: calc(100% - 80px);
  height: 200px;
  padding: 20px;
  background-color: #f5f5f5;
  border: 2px solid #0057ff;
  border-radius: 15px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.modal img {
  margin: 0 auto;
  display: block;
}
.madalClose{
  position: absolute;
  bottom: 15px;
  right: 15px;
}

@media (min-width: 690px) {
  .mainListContainer{
    width: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: revert;
    flex-wrap: wrap;
    justify-content: flex-start;
  }
  .make-list {
  max-height: 300px;
  width: 30%;
  }
  
}
